.section {
  padding-top: 25px;
  padding-bottom: 25px;
  @include md {
    padding-top: 15px;
    padding-bottom: 15px; }
  &__title {
    margin-bottom: 40px;
    @include xl {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__items {
    margin-bottom: 45px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__btn {
    margin-bottom: 30px;
    text-align: center;
    @include md {
      margin-bottom: 15px; } }
  &__top {
    background-color: $white;
    padding-top: 40px;
    padding-bottom: 35px;
    @include xl {
      padding-top: 30px;
      padding-bottom: 30px; }
    @include lg {
      padding-top: 25px;
      padding-bottom: 25px; }
    @include md {
      padding-top: 20px;
      padding-bottom: 20px; } } }
