body {
  background-color: $white-dark;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'helveticaneuecyr', Arial, sans-serif;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm-px; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-fade-in) {
    animation: fadeIn 1s; } }

.out {
  padding-top: $header-height;
  @include lg {
    padding-top: $header-height-lg; } }

.main {
  position: relative;
  background-color: $white;
  z-index: 1;
  background-color: $white-dark; }

.logo {
  font-weight: 700;
  text-transform: uppercase;
  font-size: em(20);
  display: inline-block;
  @include tr(color .3s);
  @include hover {
    color: $accent; }
  @include sm {
    font-size: $fz; }
  @include xs {
    font-size: $fz-sm; } }

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1345px;
  &--lg {
    max-width: 1790px; } }

.title-block {
  &__upttl {
    text-transform: uppercase;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } } }

.price {
  display: inline-block;
  font-size: em(18);
  text-transform: uppercase;
  color: $accent;
  &--inherit {
    color: inherit; }
  &--medium {
    font-weight: 500; }
  &--sm {
    font-size: $fz-sm; }
  &--md {
    font-size: em(21); } }

.link {
  font-weight: 500;
  @include tr(color .3s);
  @include hover {
    color: $accent; } }

.accent {
  color: $accent; }

.link-accent {
  color: $accent;
  font-weight: 500;
  font-size: $fz-sm;
  @include hover {
    text-decoration: underline; } }

.close {
  display: inline-block;
  @include icon-close(20, 1, currentColor); }
