.nav {
  text-transform: uppercase;
  font-weight: 300;
  li {
    position: relative;
    padding-left: 5px;
    padding-right: 5px;
    @include hover {
      > ul {
        @include visible; } }
    > ul {
      position: absolute;
      top: 100%;
      left: 0;
      z-index: 1;
      background-color: $c-text;
      color: $white;
      display: block;
      width: 220px;
      padding: 20px;
      font-weight: 400;
      font-size: $fz-sm;
      text-transform: none;
      @include tr(opacity .3s, visibility .3s);
      @include hidden;
      @include before {
        @include arr(20, 10, $c-text, t);
        position: absolute;
        bottom: 100%;
        left: 12px; }
      li {
        @include notlast {
          margin-bottom: 15px; } }
      a {
        display: inline;
        padding: 0 !important; } } }

  ul {
    @include flex(space-around, center);
    @include lg {
      display: block; } }
  a {
    @include tr(color .3s);
    display: inline-block;
    padding-top: 1em;
    padding-bottom: 1em;
    @include hover {
      color: $accent; }
    &.is-active {
      color: $accent; } }

  &--footer {
    text-transform: none;
    ul {
      justify-content: space-between;
      @include lg {
        display: flex; }
      @include md {
        justify-content: center;
        flex-wrap: wrap; } }
    a {
      display: inline;
      padding: 0; } } }
