.tooltip {
  position: absolute;
  z-index: 10;
  background-color: $white;
  left: -50px;
  top: calc(100% + 30px);
  right: 0;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  @include hidden;
  @include lg {
    top: calc(100% + 15px); }
  @include xs {
    position: fixed;
    left: 0 !important;
    width: 100% !important;
    top: $header-height-lg !important; }
  &.is-active {
    @include visible; } }
